/* Design System Variables */
:root {
  /* === COLORS === */
  /* Cleveland Elementary Base Colors */
  --color-navy: #001689;
  --color-periwinkle: #a0aef6;
  --color-white: #ffffff;
  --color-gray: #abb8c3;
  --color-gold: #FFD700;
  --color-green: #10B981;
  --color-coral: #FF6B6B;
  --color-midnight: #0F172A;
  
  /* Semantic Colors */
  --color-primary: var(--color-navy);
  --color-primary-light: var(--color-periwinkle);
  --color-secondary: var(--color-gold);
  --color-success: var(--color-green);
  --color-warning: #F59E0B;
  --color-danger: #EF4444;
  --color-info: #3B82F6;
  
  /* Text Colors */
  --color-text-primary: var(--color-midnight);
  --color-text-secondary: #64748B;
  --color-text-muted: #94A3B8;
  --color-text-inverse: var(--color-white);
  
  /* Background Colors */
  --color-bg-primary: var(--color-white);
  --color-bg-secondary: #F8FAFC;
  --color-bg-muted: #F1F5F9;
  --color-bg-overlay: rgba(0, 0, 0, 0.5);
  
  /* Border Colors */
  --color-border: #E2E8F0;
  --color-border-muted: #F1F5F9;
  --color-border-focus: var(--color-primary);
  
  /* === SPACING === */
  --space-1: 0.25rem;   /* 4px */
  --space-2: 0.5rem;    /* 8px */
  --space-3: 0.75rem;   /* 12px */
  --space-4: 1rem;      /* 16px */
  --space-5: 1.25rem;   /* 20px */
  --space-6: 1.5rem;    /* 24px */
  --space-8: 2rem;      /* 32px */
  --space-10: 2.5rem;   /* 40px */
  --space-12: 3rem;     /* 48px */
  --space-16: 4rem;     /* 64px */
  --space-20: 5rem;     /* 80px */
  
  /* === TYPOGRAPHY === */
  --font-family-base: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  --font-family-mono: 'SF Mono', Monaco, Inconsolata, 'Roboto Mono', monospace;
  
  /* Font Sizes */
  --font-size-xs: 0.75rem;     /* 12px */
  --font-size-sm: 0.875rem;    /* 14px */
  --font-size-base: 1rem;      /* 16px */
  --font-size-lg: 1.125rem;    /* 18px */
  --font-size-xl: 1.25rem;     /* 20px */
  --font-size-2xl: 1.5rem;     /* 24px */
  --font-size-3xl: 1.875rem;   /* 30px */
  --font-size-4xl: 2.25rem;    /* 36px */
  --font-size-5xl: 3rem;       /* 48px */
  
  /* Font Weights */
  --font-weight-light: 300;
  --font-weight-normal: 400;
  --font-weight-medium: 500;
  --font-weight-semibold: 600;
  --font-weight-bold: 700;
  --font-weight-extrabold: 800;
  
  /* Line Heights */
  --line-height-tight: 1.25;
  --line-height-snug: 1.375;
  --line-height-normal: 1.5;
  --line-height-relaxed: 1.625;
  --line-height-loose: 2;
  
  /* === BORDERS === */
  --border-width: 1px;
  --border-width-2: 2px;
  --border-width-4: 4px;
  
  /* Border Radius */
  --radius-none: 0;
  --radius-sm: 0.125rem;   /* 2px */
  --radius-base: 0.25rem;  /* 4px */
  --radius-md: 0.375rem;   /* 6px */
  --radius-lg: 0.5rem;     /* 8px */
  --radius-xl: 0.75rem;    /* 12px */
  --radius-2xl: 1rem;      /* 16px */
  --radius-full: 9999px;
  
  /* === SHADOWS === */
  --shadow-sm: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  --shadow-base: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  --shadow-md: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  --shadow-xl: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  --shadow-2xl: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
  
  /* === TRANSITIONS === */
  --transition-fast: 150ms ease;
  --transition-base: 300ms ease;
  --transition-slow: 500ms ease;
  
  /* === Z-INDEX === */
  --z-dropdown: 1000;
  --z-sticky: 1020;
  --z-fixed: 1030;
  --z-modal-backdrop: 1040;
  --z-modal: 1050;
  --z-popover: 1060;
  --z-tooltip: 1070;
}

/* === THEME VARIANTS === */
/* Claude Code Theme (Default) */
[data-tool="claude"] {
  --color-primary: var(--color-navy);
  --color-primary-light: var(--color-periwinkle);
}

/* Code Puppy Theme */
[data-tool="puppy"] {
  --color-primary: #0D7377;
  --color-primary-light: #14FFEC;
}

/* === DARK MODE === */
[data-theme="dark"] {
  --color-text-primary: var(--color-white);
  --color-text-secondary: #CBD5E1;
  --color-text-muted: #64748B;
  
  --color-bg-primary: #1E293B;
  --color-bg-secondary: #0F172A;
  --color-bg-muted: #334155;
  
  --color-border: #334155;
  --color-border-muted: #475569;
}

[data-theme="dark"][data-tool="claude"] {
  --color-primary: #3B82F6;
  --color-primary-light: #60A5FA;
}

[data-theme="dark"][data-tool="puppy"] {
  --color-primary: #14B8A6;
  --color-primary-light: #5EEAD4;
}